орская Академия художеств.\n             На русской почве неповторимое своеобразие приобретают барокко, рококо, классицизм, сентиментализм. Искусство классицизма воплотило идеалы эпохи Просвещения, стремившейся к познанию мира силами просвещенного разума и чувства. В иерархии академических жанров главная роль принадлежала исторической картине. Неоспоримы художественные достоинства портретов, созданных Фёдором Рокотовым, Дмитрием Левицким, Владимиром Боровиковским.  </p></div> <div class=\"news-page-content-info-description\" data-v-b1be5096><p data-v-b1be5096>Мы прославляем самобытный гений отечественных художников и осознаем большую ответственность за продолжение традиций Галереи, связанных с сохранением коллекции для будущих поколений. </p></div> <div class=\"news-page-content-info-description\" data-v-b1be5096><div class=\"news-page-content-info-description-item\" data-v-b1be5096><p class=\"number\" data-v-b1be5096>1.</p>исследовать, сохранять, представлять и популяризировать искус- ство России;</div> <div class=\"news-page-content-info-description-item\" data-v-b1be5096><p class=\"number\" data-v-b1be5096>2.</p>формировать российскую культурную идентичность, привлекая внимание общества к той важной роли, которую играют в ней искусство. Продолжать дело П.М.Третьякова, знакомя жителей страны с отечественным художественным наследием;</div> <div class=\"news-page-content-info-description-item\" data-v-b1be5096><p class=\"number\" data-v-b1be5096>3.</p>делать жизнь людей лучше, открывая широкий доступ к шедеврам русского и мирового искусства.</div></div> <div class=\"news-page-content-info-description\" data-v-b1be5096><p data-v-b1be5096>Мы прославляем самобытный гений отечественных художников и осознаем большую ответственность за продолжение традиций Галереи, связанных с сохранением коллекции для будущих поколений. </p></div> <div class=\"news-page-content-info-description\" data-v-b1be5096><div class=\"news-page-content-info-description-item\" data-v-b1be5096><p cl